
// 2
let MVVM;

class Store {
  constructor(options) {
    // state
    // this.$store.state.xx
    // 
    this._vm = new MVVM({
      data: {
        $store: options.state
      }
    })
    
    this._mutations = options.mutations
    this._actions = options.actions

    // this
    this.commit = this.commit.bind(this)
    this.dispatch = this.dispatch.bind(this)
  }

  // 
  get state() {
    return this._vm.$store
  }

  set state(val) {
    console.error('state');

  }
  
  // commitstate
  commit(type, payload) {
    // mutations
    const entry = this._mutations[type]
    if (!entry) {
      console.error('mutaion');
      return
    }
    entry(this.state, payload)
  }

  dispatch(type, payload) {
    const entry = this._actions[type]

    if (!entry) {
      console.error('action');
      return
    }

    // store
    entry(this, payload)
  }
}

function install(_MVVM){
  MVVM = _MVVM
  // store
//   MVVM.mixin({
//     beforeCreate() {
//       if (this.$options.store) {
//         MVVM.prototype.$store = this.$options.store
//       }
//     }
//   })
}

// { Store, install }MVVMx
// install
export default { Store, install }


