// 
// 1.Router
// 2.router-linkrouter-view

let Vue;

class VueRouter {
    // 
    // 1.url
    constructor(options) {
        this.$options = options;
        // pathroute
        // 
        this.routeMap = {}
        this.$options.routes.forEach(route => {
            this.routeMap[route.path] = route
        })

        // 
        // currentrerender
        // Vue.util.defineReactive(this, 'current', '')

        // onHashChangethis
        window.addEventListener('hashchange', this.onHashChange.bind(this))
        window.addEventListener('load', this.onHashChange.bind(this))
    }

    onHashChange () {
        console.log(window.location.hash);
        this.current = window.location.hash.slice(1) || '/'
        console.log(this.routeMap[this.current])
        var dom = document.getElementById('router')
        dom.innerHTML = '<router-view></router-view>'
        var a = new Vue(this.routeMap[this.current].component)
        a.$mount('router-view')
    }
}

// install
// Vue
VueRouter.install = function (MVVM) {
    // VueVueRouter
    Vue = MVVM

}

export default VueRouter


